.list {
  padding: 15px;
  display: flex;
  align-items: center;
  list-style: none;
}

.item:not(:last-child) {
  margin-right: 20px;
}
.itemLink {
  text-transform: uppercase;
  text-decoration: none;
  color: #000;
  cursor: pointer;
}

.itemLinkActive {
  color: firebrick;
}
